:root {

  --orange-soda: #f06543ff;
  --platinum: #e8e9ebff;
  --alabaster: #e0dfd5ff;
  --onyx: #313638ff;
  --sandy-brown: #f09d51ff;

}

#colors() {
  primary: #f06543;
  secondary: #313638;
  accent1: #e8e9eb;
  accent2: #f09d51;
  base1: #f2f3f5; //#e8e9eb;
}

// @import 'icons.less';
@import './bootstrap-icons.less';

@font-face {
  font-family: 'Noto Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Noto Sans'),
    local('NotoSans-Regular'),
    url('./fonts/NotoSans-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Noto Sans Bold';
  font-style: bold;
  font-weight: 700;
  src: local('Noto Sans Bold'),
    local('NotoSans-Bold'),
    url('./fonts/NotoSans-Bold.ttf') format('truetype');
}

body {
  overflow: hidden;
  margin: 0;
  padding: 0;
  background-color: #colors[base1];
  font-family: Helvetica, sans-serif;
  color: #colors[secondary];
  font-size: 10pt;
  font-family: 'Noto Sans'
}


#appContainer {
  height:100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;

  .navbar {
    flex-basis: auto;
    flex-grow: 0;
    padding-left: 1rem;
    // padding: 1rem;
    // margin: 2rem;
    .title {
      font-size: 1.3rem;
      display: inline-block;
      font-weight: bold;
      height: 100%;
      padding-right: 1rem;
    }
    .navbar-block {
      &.left {
        margin-right: 1em;
      }
      &.right {
        float: right;
        margin-left: 1em;
      }
      display: inline-block;

    }
    .navbar-item {
      display: inline-block;
      box-sizing: border-box;
      a {
        cursor: pointer;
        display: inline-block;
        box-sizing: border-box;
        padding: 0.5rem;
        height: 100%;
        &:hover {
          background-color: #colors[primary];
          color: #colors[secondary];
        }

        &[active='true'] {
          background-color: #colors[primary];
          color: #colors[secondary];

          &:hover {
            background-color: #colors[secondary];
            color: #colors[primary];
          }
        }
      }
      &.active {
        background-color: #colors[primary];
        color: #colors[secondary];
      }
    }
    .material-icons {
      vertical-align: top;
    }
  }

  #appPrimary {
    flex-basis: 0;
    display: flex;
    flex-direction: row;
    height: 10rem;
    flex-grow: 100;
    .navbar{
      font-size: 0.8em;
    }
    #inputContainer {
      display: flex;
      flex-direction: column;

      .editorContainer {

        width: 100%;
        display: flex;
        flex-direction: column;
        flex-grow: 1;

        &.hidden {
          display: none;
        }

        .editor {
            // width: 100%;
            // height: 100%;
            position: relative;
            flex-basis: 100%;
        }
      }
    };

    #outputContainer {
      display: flex;
      flex-direction: column;


      #renderOutputContainer {
        overflow: auto;
        iframe {
          height: 100%;
          width: 100%;
        }
      };
      #errorsOutputContainer {
        overflow:auto;

        .output-log {
          padding-left: 4px;
          padding-right: 4px;
          font-size: 0.8em;
          font-family: monospace;
          .log-entry {
            padding-left: 16px;
            padding-top: 3px;
            padding-bottom: 3px;
            border-bottom: 1px solid #colors[secondary];
            &.LOG {

            }
            &.ERROR {
              color: red;
            }
            &.DEBUG {

            }
          }
        }
      };
    };
  };
}

.gutter {
  background-color: #ddd;
  cursor: row-resize;
}

.gutter.gutter-horizontal {
  cursor: col-resize;
}

.gutter.gutter-vertical {
  cursor: row-resize;
}


.modal {
 display: none;
 position: fixed;
 visibility: hidden;
 z-index: 40;
 left: 0;
 top: 0;
 width: 100%;
 height: 100%;
 overflow: auto;
 background-color: #colors[base1];
 background-color: fade(#colors[base1],50%);

 &.active {
   display: block;
   visibility: visible;
 }

 .modal-content {
  background-color: #colors[base1];
  margin: 5em auto;
  padding: 1em;
  border: 1px solid #888;
  width: 70%;

  .modal-header {
    display: flex;

    border-bottom: 1px solid darken(#colors[base1],25%);

    h1 {
      flex-basis: 100%;
    }

    .modal-close {
     font-size: 28px;
     font-weight: bold;
     i {
       color: #colors[primary];
     }
    }
  }

  .modal-body {
    padding: 2px 16px;
    color: #colors[secondary];
  }

  .modal-footer {
    margin-top: 0.5em;
    padding-top: 1em;
    padding-bottom: 0.5em;
    border-top: 1px solid darken(#colors[base1],25%);
  }


 }
}



.click-to-edit-input {

  .display{
    &:hover {
      border-bottom: 1px solid #colors[primary];
    }
  }
}

button {
  border-style: none;
  background-color: #colors[primary];
  padding: 0.75em;
  margin: 0.5em;

  box-shadow: 2px 2px 5px darken(#colors[base1],25%);
  &.secondary {
    background-color: #colors[base1];
    color: #colors[primary];
  }

  &:hover {
    background-color: #colors[secondary];
    color: #colors[primary];
  }
}

.input-form {
  .section {
    padding-top: 0.5em;
    padding-bottom: 0.5em;

    &.controls {
      button {
        display: inline;
        margin-right: 1em;
      }
    }
  }
  .field {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    display: block;
    label {
      display: inline;
    }
    label + *{
      margin-left: 1em;
      display: inline;
    }
  }
}
